.App {
  font-family: sans;
  text-align: center;
}

.stats {
  height: 100px;
  overflow-y: auto;
  margin-top: 8px;
}

.e-card {
  display: block;
  width: 180px;
  background-color: #ffe4c4;
  padding: 10px;
  border-radius: 12px;
  text-align: justify;
  border: 2px solid #a17743;
}

.e-text {
  background-color: #fff;
  padding: 6px;
  margin-bottom: 8px;
  border-radius: 12px;
}

.e-effects {
  border: 2px solid rgb(117, 167, 72);
  border-radius: 6px;
}

.e-effect {
  display: flex;
  justify-content: space-between;
  padding: 3px;
}

.e-effect span:nth-child(2) {
  background-color: #fff;
  padding: 2px;
  border-radius: 3px;
}
